.header {
    width: 100%;
    height: 50px;
    background: #2b5295;
    padding: 0 30px;
    box-sizing: border-box;
}

.header h1, .header h1 a, .header h1 a img {
    display: inline-block;
    height: 100%;
    width: auto;
}

.header h1, .header .headerNav {
    margin-bottom: 0;
}

.header .headerNav{
    margin-left: 80px;
    height: 100%;
}

.header .headerNav a {
    display: inline-block;
    width: 100px;
    height: 100%;
    text-align: center;
    line-height: 50px;
    color: #fff;
    text-decoration: none;
}

.header .headerNav a.active {
    background: #1e3d73;
}

.header .userMsg {
    height: 100%;
}

.header .userMsg .user{
    height: 100%;
    color:#fff;
    line-height: 50px;
}

.header .userMsg .msg {
    height: 100%;
    line-height: 50px;
    color:#fff;
    margin-left: 80px;
}

.header .userMsg .msg a {
    display: inline-block;
    height: 100%;
    line-height: 50px;
}

.header .userMsg .msg a .ant-badge {
    vertical-align: middle;
}

.header .userMsg .msg a .text {
    margin-left: 10px;
    color: #fff;
}
.header .userMsg .user, .header .userMsg .msg {
    float: left;
}

.logo, .headerNav {
    float: left;
}

.userMsg{
    float: right
}

.headerNav li {
    float: left;
}