.aside {
    width: 250px;
    height: 100%;
    background: #c5cfea;
    float: left;
    padding-top: 20px;
}

.aside a {
    display: block;
    padding:20px 0 20px 50px;
    box-sizing: border-box;
    color: #1d3c73;
    text-decoration: none;
    font-size: 16px;
    border-left: 5px solid transparent;
}

.aside a.active{
    background: #aebcdf;
    border-left: 5px solid #0057ff;
    color: #0057ff;
}