.header {
    width: 100%;
    height: 50px;
    background: #2b5295;
    padding: 0 30px;
    box-sizing: border-box;
}

.header h1, .header h1 a, .header h1 a img {
    display: inline-block;
    height: 100%;
    width: auto;
}

.header h1, .header .headerNav {
    margin-bottom: 0;
}

.header .headerNav{
    margin-left: 80px;
    height: 100%;
}

.header .headerNav a {
    display: inline-block;
    width: 100px;
    height: 100%;
    text-align: center;
    line-height: 50px;
    color: #fff;
    text-decoration: none;
}

.header .headerNav a.active {
    background: #1e3d73;
}

.header .userMsg {
    height: 100%;
}

.header .userMsg .user{
    height: 100%;
    color:#fff;
    line-height: 50px;
}

.header .userMsg .msg {
    height: 100%;
    line-height: 50px;
    color:#fff;
    margin-left: 80px;
}

.header .userMsg .msg a {
    display: inline-block;
    height: 100%;
    line-height: 50px;
}

.header .userMsg .msg a .ant-badge {
    vertical-align: middle;
}

.header .userMsg .msg a .text {
    margin-left: 10px;
    color: #fff;
}
.header .userMsg .user, .header .userMsg .msg {
    float: left;
}

.logo, .headerNav {
    float: left;
}

.userMsg{
    float: right
}

.headerNav li {
    float: left;
}
.home {
    width: 100%;
    height: 100%;
    padding-top: 50px;
    display: flex;
}

.home .user_info {
    width: 300px;
    height: 100%;
    float: left;
    flex-shrink: 0;
}

.home .user_info .userHead {
    width: 180px;
    height: 180px;
    margin: 0 auto;
    border-radius: 50%;
}

.home .user_info .userHead img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.home .user_info p {
    width: 100%;
    padding-left: 100px;
    margin: 10px auto;
    font-size: 16px;
}

.home .user_info .userName {
    margin-top: 30px;
}

.home .home_content {
    flex: 1 1;
    margin-left: 15px;
}

.home .home_content .card {
    width: 500px;
    height: 300px;
    border-radius: 30px;
    background: #fff;
    float: left;
    margin-right: 50px;
    margin-bottom: 50px;
    padding:15px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.home .home_content .card .card_top {
    margin-bottom: 0;
    display: flex;
    height: 25px;
    flex-shrink: 0;
}

.home .home_content .card .card_top span {
    color: #0c44a1;
    padding-left: 20px;
    border-left: 5px solid #0c44a1;
    height: 100%;
    line-height: 25px;
}

.home .home_content .card .card_cont {
    flex:1 1;
    background: #ff0;
}
.administration {
    width: 100%;
    height: 100%;
}

.administration .administrationsection{
    float: left
}
.aside {
    width: 250px;
    height: 100%;
    background: #c5cfea;
    float: left;
    padding-top: 20px;
}

.aside a {
    display: block;
    padding:20px 0 20px 50px;
    box-sizing: border-box;
    color: #1d3c73;
    text-decoration: none;
    font-size: 16px;
    border-left: 5px solid transparent;
}

.aside a.active{
    background: #aebcdf;
    border-left: 5px solid #0057ff;
    color: #0057ff;
}
* {
  padding: 0;
  margin: 0;
  list-style: none;
}

html, body, #root{
  width: 100%;
  height: 100%
}

.wrap {
  width: 100%;
  height: 100%;
}
.section  {
  width: 100%;
  height: calc(100% - 50px);
  background: #f7f7f7;
}

