.home {
    width: 100%;
    height: 100%;
    padding-top: 50px;
    display: flex;
}

.home .user_info {
    width: 300px;
    height: 100%;
    float: left;
    flex-shrink: 0;
}

.home .user_info .userHead {
    width: 180px;
    height: 180px;
    margin: 0 auto;
    border-radius: 50%;
}

.home .user_info .userHead img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.home .user_info p {
    width: 100%;
    padding-left: 100px;
    margin: 10px auto;
    font-size: 16px;
}

.home .user_info .userName {
    margin-top: 30px;
}

.home .home_content {
    flex: 1;
    margin-left: 15px;
}

.home .home_content .card {
    width: 500px;
    height: 300px;
    border-radius: 30px;
    background: #fff;
    float: left;
    margin-right: 50px;
    margin-bottom: 50px;
    padding:15px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.home .home_content .card .card_top {
    margin-bottom: 0;
    display: flex;
    height: 25px;
    flex-shrink: 0;
}

.home .home_content .card .card_top span {
    color: #0c44a1;
    padding-left: 20px;
    border-left: 5px solid #0c44a1;
    height: 100%;
    line-height: 25px;
}

.home .home_content .card .card_cont {
    flex:1;
    background: #ff0;
}